.navbar {
    height: 10vh;
    background: #5d8012;
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
  .navbar-logo img{
    width: 10vh;
    clip-path: circle(45%);
    filter: saturate(300%);
  }
  .navbar-logo span{
    flex-grow: 1;
    font-family: cursive;
  }
  .navbar-logo {
    color: white;
    text-decoration: none;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 10rem;
    gap: 10px;
  }
  
  .nav-items {
    display: flex;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: flex-end;
    margin-right: 2rem;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 10vh;
  }
  
  .nav-item a {
    text-decoration: none;
    color: white;
    font-size: 1.1rem;
    margin-right: 2rem;
    padding: 6px 16px;
    border-radius: 5px;
  }
  
  .nav-item a:hover {
    background: rgb(124, 212, 0);
  }